    ScanProductPage.prototype.startScanBarcode1 = function (ackStr, checkForBin) {
        var svc = this;
        this.productInfoBack = this.productInfo;
        var confirmedPick = Number(svc.confirmedPick);
        this.mobileAppSystem.confirmProductQty(this.user.orderInfo.orderBarcode, this.productInfo.stockCode, this.productInfo.binLocation, Number(this.confirmedPick), this.productBarCode, this.user.orderInfo.zone, this.user.hasTotes, ackStr, checkForBin, svc.laneStockItem, function (res) {
            svc.clearBarcodeScan();
            if (res.result.statusCode == 200) {
                svc.laneStockItem = res.result.laneStockItem;
                if (svc.laneStockItem == 'Y') {
                    svc.alertService.doAlertWithtimeOut('', res.result.statusMsg, 2000).then(function (yes) {
                        if (res.result.orderNumber == null) {
                            if (res.result.go2ScanOrderPage == 'Y') {
                                svc.navCtrl.setRoot('ScanOrderPage');
                                return;
                            }
                        }
                        else {
                            svc.setQtyInformation(res.result);
                        }
                    });
                }
                else {
                    if (res.result.orderNumber == null) {
                        if (res.result.go2ScanOrderPage == 'Y') {
                            svc.navCtrl.setRoot('ScanOrderPage');
                            return;
                        }
                    }
                    else {
                        svc.setQtyInformation(res.result);
                    }
                }
            }
            else if (res.result.statusCode == 400) {
                if (checkForBin == 'Y') {
                    svc.alertService.doAlert(res.result.statusMsg, '', 'OK');
                }
                else {
                    svc.inputBinLocationCode(ackStr, res.result.statusMsg, false);
                }
            }
        });
    };
    ScanProductPage.prototype.onChangedProductBarCode1 = function (val) {
        if (this.scanStarted == false)
            return;
        if (this.productInfo.pickUnitSensitive == 'Y' && this.pickUnitSensitive == false)
            return;
        if (val == '')
            return;
        this.productBarCode = val;
        this.productBarCode1Bk = val;
        this.startScanBarcode1('N', 'N');
    };
